<template>
  <v-data-table
    :headers="headers"
    :items="items"
    :options.sync="options"
    :server-items-length="total"
    :loading="loading"
    class="elevation-1"
  >
    <slot name="item"></slot>
  </v-data-table>
</template>

<script>
  export default {
    name: "Server-Table",
    props: {
      headers:[],
      items: [],
      loading:true,
      total: 0,
    },
    data() {
      return {
        options: {},
      };
    },
    watch: {
      options: {
        handler() {
          this.$emit('Refresh', this.options);
        },
        deep: true,
      },
    },
    mounted() {
     this.$emit('Refresh', this.options);
    },
  }
</script>

